.notification-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.notification-dialog {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    width: 90%;
    text-align: center;
}

.notification-dialog.success {
    border-left: 4px solid #4CAF50;
}

.notification-dialog.error {
    border-left: 4px solid #f44336;
}

.notification-dialog.warning {
    border-left: 4px solid #ff9800;
}

.notification-dialog.info {
    border-left: 4px solid #2196F3;
}

.notification-dialog.game_over {
    border-left: 4px solid #d32f2f;
}

.arnold-dialog {
    max-width: 600px;
}

.arnold-image-container {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
}

.arnold-image {
    max-width: 100%;
    max-height: 250px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.notification-dialog p {
    margin-bottom: 20px;
    line-height: 1.5;
}

.dialog-buttons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 20px;
}

.notification-dialog button {
    padding: 8px 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.2s;
    min-width: 100px;
    height: 36px;
    margin: 0;
}

.notification-dialog button.action-button {
    background-color: #4CAF50;
    color: white;
}

.notification-dialog button.action-button:hover {
    background-color: #45a049;
}

.notification-dialog button.action-button.warning {
    background-color: #ff9800;
}

.notification-dialog button.action-button.warning:hover {
    background-color: #f57c00;
}

.notification-dialog button.action-button.error {
    background-color: #f44336;
}

.notification-dialog button.action-button.error:hover {
    background-color: #e53935;
}

.notification-dialog button.cancel-button {
    background-color: #f5f5f5;
    color: #666;
    border: 1px solid #ddd;
}

.notification-dialog button.cancel-button:hover {
    background-color: #e0e0e0;
}

.notification-dialog button.restart-button {
    background-color: #d32f2f;
    color: white;
}

.notification-dialog button.restart-button:hover {
    background-color: #b71c1c;
} 