.wandering-animal {
    position: fixed;
    transition: all 2s ease-in-out;
    z-index: 950;
}

.wandering-animal-image {
    width: 48px;
    height: 48px;
    filter: drop-shadow(0 0 4px rgba(0,0,0,0.3));
    animation: bounce 1s infinite alternate ease-in-out;
}

.wandering-animal.disabled {
    opacity: 0.6;
}

@keyframes bounce {
    from {
        transform: translateY(0);
    }
    to {
        transform: translateY(-10px);
    }
}

.wandering-animal:hover .wandering-animal-image {
    transform: scale(1.1);
}
