.farm-buildings {
    background-color: #f4f4f4;
    padding: 15px;
    border-radius: 8px;
    margin: 20px 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.buildings-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    padding: 10px;
}

.building {
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: transform 0.3s ease;
    position: relative;
    cursor: pointer;
}

.building img {
    max-width: 90px;
    max-height: 90px;
    object-fit: contain;
    border-radius: 4px;
    filter: drop-shadow(2px 2px 3px rgba(0, 0, 0, 0.2));
}

.building-label {
    margin-top: 8px;
    font-size: 0.9rem;
    font-weight: bold;
    color: #555;
}

.building:hover {
    transform: scale(1.05);
}

.building .building-tooltip {
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 8px 12px;
    border-radius: 4px;
    font-size: 14px;
    white-space: nowrap;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s, visibility 0.2s;
    z-index: 10;
    pointer-events: none;
    margin-bottom: 8px;
}

.building .building-tooltip::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border-width: 5px;
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.8) transparent transparent transparent;
}

.building:hover .building-tooltip {
    opacity: 1;
    visibility: visible;
}

/* Animation for new buildings */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.barn, .tractor, .farmhand, .dog {
    animation: fadeIn 0.5s ease-out forwards;
}

/* Special animation for farmhand */
.farmhand img {
    animation: wave 2s ease-in-out infinite;
}

/* Special animation for dog */
.dog img {
    animation: wag 2s ease-in-out infinite;
}

@keyframes wave {
    0%, 100% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(5deg);
    }
    75% {
        transform: rotate(-5deg);
    }
}

@keyframes wag {
    0%, 100% {
        transform: rotate(0deg);
    }
    25% {
        transform: rotate(5deg);
    }
    75% {
        transform: rotate(-5deg);
    }
}

/* When in header, use a different styling */
.game-header .farm-buildings {
    background: none;
    box-shadow: none;
    margin-top: 0;
    padding: 0;
}

.game-header .buildings-container {
    background-color: rgba(255, 255, 255, 0.1);
    padding: 15px;
    border-radius: 8px;
    margin-top: 0;
}

.game-header .building-label {
    color: rgba(255, 255, 255, 0.9);
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .building img {
        max-width: 70px;
        max-height: 70px;
    }
    
    .buildings-container {
        gap: 10px;
    }
}

/* Optional: Different background for a more pastoral look */
@media (min-width: 768px) {
    .farm-buildings {
        background: linear-gradient(to bottom, #87CEEB 0%, #87CEEB 60%, #8FBC8F 60%, #8FBC8F 100%);
    }
}

.building.farmhand {
    cursor: pointer;
    transition: transform 0.2s ease, filter 0.2s ease;
}

.building.farmhand:hover {
    transform: scale(1.1);
    filter: brightness(1.1);
}

.building.farmhand:active {
    transform: scale(0.95);
}

.building.rooster {
    transition: transform 0.2s ease;
    transform: scale(0.85);
}

.building.rooster:hover {
    transform: scale(1.1);
}

.building.rooster img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
