.intro-dialog-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.intro-dialog {
    background-color: white;
    padding: 2rem;
    border-radius: 8px;
    max-width: 600px;
    width: 90%;
    max-height: 90vh;
    overflow-y: auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.intro-dialog h2 {
    color: #2c3e50;
    margin-bottom: 1.5rem;
    text-align: center;
}

.intro-content {
    margin-bottom: 2rem;
}

.intro-content p {
    margin-bottom: 1rem;
    line-height: 1.6;
    color: #34495e;
}

.intro-content ul {
    margin: 1rem 0;
    padding-left: 2rem;
}

.intro-content li {
    margin-bottom: 0.5rem;
    color: #34495e;
}

.warning {
    background-color: #fff3cd;
    border-left: 4px solid #ffc107;
    padding: 1rem;
    margin: 1.5rem 0;
    color: #856404;
}

.intro-dialog button {
    display: block;
    width: 100%;
    padding: 1rem;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1.1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.intro-dialog button:hover {
    background-color: #45a049;
} 